var THREE = require('three');

var PyramidBloomPass = require('./PyramidBloomPass')(THREE);



/* global document, d3, alert, $, THREE, TWEEN, requestAnimationFrame, glslify */

/*
 *
 *
 * Laden der Visualisation
 *
 *
 */

loadVisualization()

function loadVisualization () {

  var camera, scene, renderer, composer;
  var object, light;
  var points_fluuuid;
  var clock;

  init();
  animate();

  function init() {

    clock = new THREE.Clock();

    renderer = new THREE.WebGLRenderer({alpha: true, antialias: true});
    renderer.setPixelRatio( window.devicePixelRatio );
    renderer.setSize( window.innerWidth, window.innerHeight );
    document.body.appendChild( renderer.domElement );

    /**
     *
     * Camera & Scene setup
     *
     */

    function setupCamera(){
      var near = 1;
      var far = 700;
      var someStrangeFactor = 5;
      var left = window.innerWidth / - someStrangeFactor;
      var right = window.innerWidth / someStrangeFactor;
      var top = window.innerHeight / someStrangeFactor;
      var bottom = window.innerHeight / - someStrangeFactor;
      if(!camera){
        camera = new THREE.OrthographicCamera(left, right, top, bottom, near, far );
      }else{
        camera.left = left;
        camera.right = right;
        camera.top = top;
        camera.bottom = bottom;
      }
    }
    setupCamera();

    camera.position.z = 400;

    scene = new THREE.Scene();
    scene.fog = new THREE.Fog( 0x000000, 1, 1000 );

    // root object

    object = new THREE.Object3D();

    /**
     *
     * Particle system by FLUUUID
     *
     */

    var shader_fluuuid = new THREE.ShaderMaterial({
      uniforms: {
        color : {type: 'c', value: new THREE.Color(0xffffff)},
        time  : {type : 'f', value: 0},
      },
      transparent    : true,
      vertexShader   : "#define GLSLIFY 1\n// particle_vert by FLUUUID\n\n//\n// Description : Array and textureless GLSL 2D/3D/4D simplex\n//               noise functions.\n//      Author : Ian McEwan, Ashima Arts.\n//  Maintainer : ijm\n//     Lastmod : 20110822 (ijm)\n//     License : Copyright (C) 2011 Ashima Arts. All rights reserved.\n//               Distributed under the MIT License. See LICENSE file.\n//               https://github.com/ashima/webgl-noise\n//\n\nvec3 mod289(vec3 x) {\n  return x - floor(x * (1.0 / 289.0)) * 289.0;\n}\n\nvec4 mod289(vec4 x) {\n  return x - floor(x * (1.0 / 289.0)) * 289.0;\n}\n\nvec4 permute(vec4 x) {\n     return mod289(((x*34.0)+1.0)*x);\n}\n\nvec4 taylorInvSqrt(vec4 r)\n{\n  return 1.79284291400159 - 0.85373472095314 * r;\n}\n\nfloat snoise(vec3 v)\n  {\n  const vec2  C = vec2(1.0/6.0, 1.0/3.0) ;\n  const vec4  D = vec4(0.0, 0.5, 1.0, 2.0);\n\n// First corner\n  vec3 i  = floor(v + dot(v, C.yyy) );\n  vec3 x0 =   v - i + dot(i, C.xxx) ;\n\n// Other corners\n  vec3 g = step(x0.yzx, x0.xyz);\n  vec3 l = 1.0 - g;\n  vec3 i1 = min( g.xyz, l.zxy );\n  vec3 i2 = max( g.xyz, l.zxy );\n\n  //   x0 = x0 - 0.0 + 0.0 * C.xxx;\n  //   x1 = x0 - i1  + 1.0 * C.xxx;\n  //   x2 = x0 - i2  + 2.0 * C.xxx;\n  //   x3 = x0 - 1.0 + 3.0 * C.xxx;\n  vec3 x1 = x0 - i1 + C.xxx;\n  vec3 x2 = x0 - i2 + C.yyy; // 2.0*C.x = 1/3 = C.y\n  vec3 x3 = x0 - D.yyy;      // -1.0+3.0*C.x = -0.5 = -D.y\n\n// Permutations\n  i = mod289(i);\n  vec4 p = permute( permute( permute(\n             i.z + vec4(0.0, i1.z, i2.z, 1.0 ))\n           + i.y + vec4(0.0, i1.y, i2.y, 1.0 ))\n           + i.x + vec4(0.0, i1.x, i2.x, 1.0 ));\n\n// Gradients: 7x7 points over a square, mapped onto an octahedron.\n// The ring size 17*17 = 289 is close to a multiple of 49 (49*6 = 294)\n  float n_ = 0.142857142857; // 1.0/7.0\n  vec3  ns = n_ * D.wyz - D.xzx;\n\n  vec4 j = p - 49.0 * floor(p * ns.z * ns.z);  //  mod(p,7*7)\n\n  vec4 x_ = floor(j * ns.z);\n  vec4 y_ = floor(j - 7.0 * x_ );    // mod(j,N)\n\n  vec4 x = x_ *ns.x + ns.yyyy;\n  vec4 y = y_ *ns.x + ns.yyyy;\n  vec4 h = 1.0 - abs(x) - abs(y);\n\n  vec4 b0 = vec4( x.xy, y.xy );\n  vec4 b1 = vec4( x.zw, y.zw );\n\n  //vec4 s0 = vec4(lessThan(b0,0.0))*2.0 - 1.0;\n  //vec4 s1 = vec4(lessThan(b1,0.0))*2.0 - 1.0;\n  vec4 s0 = floor(b0)*2.0 + 1.0;\n  vec4 s1 = floor(b1)*2.0 + 1.0;\n  vec4 sh = -step(h, vec4(0.0));\n\n  vec4 a0 = b0.xzyw + s0.xzyw*sh.xxyy ;\n  vec4 a1 = b1.xzyw + s1.xzyw*sh.zzww ;\n\n  vec3 p0 = vec3(a0.xy,h.x);\n  vec3 p1 = vec3(a0.zw,h.y);\n  vec3 p2 = vec3(a1.xy,h.z);\n  vec3 p3 = vec3(a1.zw,h.w);\n\n//Normalise gradients\n  vec4 norm = taylorInvSqrt(vec4(dot(p0,p0), dot(p1,p1), dot(p2, p2), dot(p3,p3)));\n  p0 *= norm.x;\n  p1 *= norm.y;\n  p2 *= norm.z;\n  p3 *= norm.w;\n\n// Mix final noise value\n  vec4 m = max(0.6 - vec4(dot(x0,x0), dot(x1,x1), dot(x2,x2), dot(x3,x3)), 0.0);\n  m = m * m;\n  return 42.0 * dot( m*m, vec4( dot(p0,x0), dot(p1,x1),\n                                dot(p2,x2), dot(p3,x3) ) );\n  }\n\nvec3 snoiseVec3( vec3 x ){\n\n  float s  = snoise(vec3( x ));\n  float s1 = snoise(vec3( x.y - 19.1 , x.z + 33.4 , x.x + 47.2 ));\n  float s2 = snoise(vec3( x.z + 74.2 , x.x - 124.5 , x.y + 99.4 ));\n  vec3 c = vec3( s , s1 , s2 );\n  return c;\n\n}\n\nvec3 curlNoise( vec3 p ){\n  \n  const float e = .1;\n  vec3 dx = vec3( e   , 0.0 , 0.0 );\n  vec3 dy = vec3( 0.0 , e   , 0.0 );\n  vec3 dz = vec3( 0.0 , 0.0 , e   );\n\n  vec3 p_x0 = snoiseVec3( p - dx );\n  vec3 p_x1 = snoiseVec3( p + dx );\n  vec3 p_y0 = snoiseVec3( p - dy );\n  vec3 p_y1 = snoiseVec3( p + dy );\n  vec3 p_z0 = snoiseVec3( p - dz );\n  vec3 p_z1 = snoiseVec3( p + dz );\n\n  float x = p_y1.z - p_y0.z - p_z1.y + p_z0.y;\n  float y = p_z1.x - p_z0.x - p_x1.z + p_x0.z;\n  float z = p_x1.y - p_x0.y - p_y1.x + p_y0.x;\n\n  const float divisor = 1.0 / ( 2.0 * e );\n  return normalize( vec3( x , y , z ) * divisor );\n\n}\n\n// varying vec2 vUv;\n// varying vec3 vPosition;\n\nuniform float time;\nattribute float _index;\n\nvoid main()\n{\n    float multiplier = -100.;\n\n    vec3 curlP = curlNoise(position + (time));\n    //vec3 curlP = curl(position);\n\n    //curlP.x = curlP.x * smoothstep(0., 200., curlP.z);\n    curlP *= multiplier;\n\n    //curlP.z = position.z;\n\n    // curlP.y = abs(curlP.y);\n    // curlP.y += 1. / _index;\n\n    // vPosition = curlP;\n    // vUv = uv;\n\n    gl_PointSize = 1.;\n\n    // transforming vec3 to vec4, because that's how matrices in 3d are vec4;\n    vec4 position = vec4(curlP, 1.0);\n\n    // translating with modelViewMatrix for rotation and scroll support!\n    vec4 mvPosition = modelViewMatrix * position;\n\n    // setting the gl_Position of points.\n    gl_Position = projectionMatrix * mvPosition;\n  }\n",
      fragmentShader : "#define GLSLIFY 1\n// particle_frag by FLUUUID\n\n// varying vec3 vPosition;\n// varying vec3 vColor;\nuniform vec3 color;\n\nvoid main() {\n\n    // float alpha = 7. - abs(distance(vPosition.xy, vec2(.0)));\n\n    gl_FragColor = vec4( color , .4 );\n    //gl_FragColor = gl_FragColor * texture2D( texture, vUv );\n}\n",
    })

    //console.log("hai")
    //console.log(shader_fluuuid.material)

    var cluster = Math.pow(512, 2);

    var maxParticles = cluster ;
    var spread = 550;

    var geometry = new THREE.BufferGeometry();

    var pPosition = new Float32Array(maxParticles * 3);
    // let _index = new Float32Array(maxParticles);

    //BOOKMARK
    for (var i = 0, a = 0; i < pPosition.length; i+=3, a++) {
      var range = {
        w: window.innerWidth /  2 / spread - (Math.random() * window.innerWidth /  2 / (spread / 2)),
        h: window.innerHeight /  2 / spread - (Math.random() * window.innerHeight /  2 / (spread / 2))
      }
      pPosition[i + 0] = range.w/1;
      pPosition[i + 1] = range.h/1;
      pPosition[i + 2] = 0;

      // _index[a] = a;

      // this.index[a] = a;
    };

    geometry.addAttribute('position', new THREE.BufferAttribute(pPosition, 3));
    // geometry.addAttribute('_index', new THREE.BufferAttribute(_index, 1));

    points_fluuuid = new THREE.Points(geometry, shader_fluuuid);
    scene.add(points_fluuuid)
    //console.log(ergebnis);

    /**
     *
     * Building Scene
     *
     */

    scene.add( object );

    // for yellow
    //var c = 0xffffff;
    //var c_l = 0x434343;

    //var testSphere = new THREE.SphereGeometry( 100.6, 128, 128 );
    var material = new THREE.MeshPhongMaterial( { color: 0xffffff, shading: THREE.FlatShading } );
    //var testMesh = new THREE.Mesh(testSphere, material);
    //object.add(testMesh);

    var wireframe = new THREE.MeshBasicMaterial( { color: 0xffffff, wireframe: true, wireframeLinewidth: 2 } );

    var tetra = new THREE.TetrahedronGeometry( 28, 0 )
    var northlane = new THREE.Mesh( tetra, material );

    //initial rotation

    object.rotation.x = Math.PI/4-Math.PI/8+Math.PI/16+Math.PI/32-Math.PI/64-Math.PI/128;
    object.rotation.y = Math.PI/4;
    object.rotation.z = 0;


    //object.add(northlane)
    northlane.scale.multiplyScalar( 1 );
    object.add(northlane)

    var stepScale = 1.3;

    var northlane2 = northlane.clone();
    var scale2 = 1+stepScale;
    northlane2.scale.set(scale2, scale2, scale2)
    northlane2.material = wireframe;
    //object.add(northlane2)

    var northlane3 = northlane.clone();
    var scale3 = scale2+stepScale;
    northlane3.scale.set(scale3, scale3, scale3)
    northlane3.material = wireframe;
    //object.add(northlane3)

    scene.add( new THREE.AmbientLight( 0x222222 ) );

    light = new THREE.DirectionalLight( 0xffffff );
    light.position.set( 1, 1, 1 );
    scene.add( light );

    /**
     *
     * postprocessing
     *
     */


    //composer = new THREE.EffectComposer( renderer );
    //composer.addPass( new THREE.RenderPass( scene, camera ) );


    /*
    var effect = new THREE.ShaderPass( THREE.DotScreenShader );
    effect.uniforms[ 'scale' ].value = 4;
    composer.addPass( effect );
    */

    /*
    var effect = new THREE.ShaderPass( THREE.RGBShiftShader );
    effect.uniforms.amount.value = 0.001;
    effect.renderToScreen = false;
    composer.addPass( effect );
    */

    //var bloom = new PyramidBloomPass();
    //composer.addPass(bloom);

    window.addEventListener( 'resize', onWindowResize, false );

  }

  function onWindowResize() {

    console.log("resized to "+window.innerWidth+" / "+window.innerHeight)

    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();

    renderer.setSize( window.innerWidth, window.innerHeight );
    //composer.setSize( window.innerWidth, window.innerHeight );



  }

  function animate() {

    requestAnimationFrame( animate );

    var particleMovementSpeed = 0.01;
    var rotationSpeed = 0.007;

    var elapsed = clock.getElapsedTime()*particleMovementSpeed;

    //TODO
    if(points_fluuuid.material){
      points_fluuuid.material.uniforms.time.value = elapsed;
    }else{
      console.log("undeinfed!")
    }

    object.rotation.x += 1.3*rotationSpeed;
    object.rotation.y += rotationSpeed;

    // use this if using composer
    //composer.render();
    // use this if not using composer
    renderer.render(scene, camera);

  }
}
